<template>
  <v-card>
    <form>
      <v-card-title>
        <span class="headline">Edit Quota</span>
      </v-card-title>
      <v-card-text>
        <span>Client Edit</span>
        <v-text-field
          label="First Name"
          name="First name"
          v-model="quota.customer.first_name"
          required
        ></v-text-field>
        <v-text-field
          label="Last Name"
          name="Last name"
          v-model="quota.customer.last_name"
          required
        ></v-text-field>
        <v-text-field
          label="Company"
          name="Company"
          v-model="quota.customer.company_name"
          required
        />
        <v-text-field
          label="Email"
          name="email"
          v-model="quota.customer.email"
          required
        ></v-text-field>
        <v-text-field
          label="Phone"
          name="phone"
          v-model="quota.customer.phone_number"
          required
        ></v-text-field>
        <v-spacer></v-spacer>
        <span>Work - Amount Edit</span><v-divider/>
        <br>
        <div v-for ="work in quota.work_vs_amount" :key="work.id">
        <v-text-field
          label="Work Title"
          name="Work Title"
          v-model="work.work.title"
          required/>
        <v-text-field
          auto-grow
          label="Description"
          name="Description"
          v-model="work.work.description"
          required/>
        <v-text-field
          label="Unit Price"
          name="unit_price"
          v-model="work.work.unit_price"
          required/>
        <v-text-field
          label="Amount"
          name="amount"
          v-model="work.amount"
          required/>
          </div>


          <v-select
    :items="status_options"
    label="Status"
    hide-details
  ></v-select>


      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="blue darken-1"
          text
          @click="updateQuota"
        >
          Update
        </v-btn>
        <v-btn
          color="blue darken-1"
          text
          @click="addFields()">
          Add Work</v-btn>
      </v-card-actions>
    </form>
  </v-card>
</template>

<script>
import axios from "@axios"
import { mdiPlus } from '@mdi/js'
export default {
  name: 'EditQuota',
  data(){
    return{
      quota: {
        work_vs_amount:[{
          work:{
            title: '',
            description: '',
            unit_price: '',
          },
          amount: '',
        }],
        customer: '',
        status: '',
      },
      status_options: ["Pending","Approved", "Approved","Rejected", "Cancelled",
        "Completed"]


    }
  },
  async mounted(){
        const content = await axios.get('/quota/'+this.$route.params.id)
        this.quota = content.data
        console.log(this.quota)
  },
  methods: {
    updateQuota(){
      const quota_data={
        work_vs_amount: this.quota.work_vs_amount,
        customer: this.quota.customer,
        status: this.quota.status,
      }
      axios
        .put("/quota/"+this.$route.params.id+"/", quota_data)
        .catch(error => { console.log(error.response.data) })
      // this.$router.push('/QuotaDetail/'+this.$route.params.id)
    },
    addFields(){
      this.quota.work_vs_amount.push({
        work: {
          title: '',
          description: '',
          unit_price: '',
        },
        amount: '',
      })
    }
  },
  setup() {
    const picker = new Date().toISOString().substr(0, 10);

    return {
      picker,
      mdiPlus
    };
  }

}
</script>

